.profile {
  height: 500px;
  width: 425px;
  background-color: #e7ecf2;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  margin: auto;
}

.description {
  min-width: 66%;
  background-color: #ffffff;
  border-radius: 5px 5px 0 0;
}

img {
  margin-top: 15px;
  padding: 15px;
  max-width: 50%;
  border-radius: 50%;
}

p {
  margin: 8px;
  color: #bbc3ca;
}

p:first-of-type {
  color: black;
  font-size: 1.4em;
  font-weight: bold;
}

p:last-of-type {
  padding-bottom: 15px;
}

.stats {
  max-width: 66%;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  background-color: #f3f6f9;
  border-radius: 0 0 5px 5px;
  padding-inline-start: 0;
}

li {
  display: flex;
  justify-content: center;
  padding: 18px;
  flex-direction: column;
  border: 1px solid #ecf0f5;
}

.label {
  color: #bbc3ca;
  padding: 5px;
}

.quantity {
  font-weight: bold;
}
